import http from '../app-utilities/http';

export const retrieveUsernameGezo = async (emailAddress) => {
  const endpoint = '/api/c0/execute/AccessRightsManagement/RetrieveUsernameGezo';
  const payload = {
    RetrieveUsernameGezo_Request: [{
      EmailAddress: emailAddress,
    }]
  };
  const response = await http.post(endpoint, payload);
  if (response.RetrieveUsernameGezo_Response.Result.Status === 'ERROR') {
    throw new Error('retrieveUsernameGezo failed');
  }
}