<template>
  <form @submit.prevent="submit">
    <div class="o-layout">
      <nn-notification v-if="status==='error'" type="error" class="u-margin-bottom--30">
        Er is iets fout gegaan. Probeer het (later) nog eens.
      </nn-notification>
      <div v-if="status==='initial' || status==='error'">
        <p>
          Vul je e-mailadres in om je gebruikersnaam op te vragen. Je ontvangt dan binnen enkele minuten een e-mail met je gebruikersnaam.
        </p>
        <div class="o-layout__row">
          <div class="o-layout__col-s-8 o-layout__col-l-6">
            <div class="u-margin-bottom--30" data-test="emailAddress">
              <nn-input-field
                name="emailAddress"
                id="emailAddress"
                v-model="emailAddress.value"
                label="E-mailadres"
                :error="emailAddress.errorMessage"
                :invalid="emailAddress.invalid"
                @blur="validateField"
              ></nn-input-field>
            </div>
          </div>
        </div>
      </div>
      <nn-spinner v-if="status==='loading'"></nn-spinner>
      <div v-if="status==='done'">
        <nn-notification type="success" class="u-margin-bottom--30">
          Je verzoek is ontvangen, we sturen je gebruikersnaam via e-mail naar je op.
        </nn-notification>
        <h4 class="u-margin--none">Ontvang je geen e-mail?</h4>
        <p>Dat is vervelend we helpen je graag verder.</p>
        <ul>
          <li>
            <span class="u-font-bold">Het kan zijn dat het ingevoerde e-mailadres niet bekend is bij ons. </span> <br/>
            Misschien heb je een typefout gemaakt of ben je onder een ander e-mailadres bekend bij ons. Probeer het nog een keer of neem contact op met jouw toegangsbeheerder.
          </li>
           <li>
            <span class="u-font-bold">Ben je misschien toegangsbeheerder?  </span> <br/>
            Dan is het niet mogelijk om online je gebruikersnaam op te vragen. Neem dan telefonisch contact met ons op.
          </li>
        </ul>
        <h4 class="u-margin--none">Telefonisch contact</h4>
        <p>Kom je er niet uit? Neem dan telefonisch contact op met onze afdeling Digital  Support. Wij zijn bereikbaar op werkdagen van 8.00 tot 17.30 uur op 070 513 07 57.</p>
        <p>Om je goed en snel te kunnen helpen vragen we je de volgende informatie gereed te houden:
          <ul>
            <li>Voor- en achternaam</li>
            <li>E-mailadres van je account</li>
            <li>Bedrijfsnaam</li>
            <li>Vestigingsadres en/of postadres</li>
            <li>Polisnummer(s)</li>
          </ul>
        </p>
      </div>
      <hr />
      <nn-button v-if="this.status==='initial' || this.status==='error'" variant="next" name="send" class="pull-right" type="submit">Versturen</nn-button>
    </div>
  </form>
</template>

<script>
import { retrieveUsernameGezo } from './api';
import { error } from '@nn/psdl-utility/src/console.js';

export default {
  name: 'corporateUsernameForgotten',
  data() {
    return {
      emailAddress: {
        value: '',
        validated: false,
        invalid: false,
        errorMessage: '',
      },
      status: 'initial',
    }
  },
  methods: {
    async submit() {
      this.validateField();
      if(this.emailAddress.invalid){
        return;
      }
      this.status = 'loading';
      try {
        await retrieveUsernameGezo(this.emailAddress.value);
        this.status = 'done';
      } catch (e) {
        this.status = 'error';
        error(e);
      }
    },
    validateField() {
      if(!this.emailAddress.value) {
        this.emailAddress.errorMessage = 'Denk je aan het invullen van dit veld.';
        this.emailAddress.invalid = true;
      } else if(!new RegExp("^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\\.[a-zA-Z0-9-]+)*$").test(this.emailAddress.value)) {
        this.emailAddress.errorMessage = 'Vul een valide email adres in.';
        this.emailAddress.invalid = true;
      } else {
        this.emailAddress.invalid = false;
      }
      this.emailAddress.validated = true;
    },
  },
}
</script>